#new-translation-button {
  margin-left: 10px;
}

#history-container h4{
  padding-top: 50px;
  text-align: center;
}

#export-container h4{
  text-align: center;
}

#export-loading {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 40px;
}

#info {
  padding-left: 10px;
  color: darkorange;
  font-size: 20px;
  cursor: pointer;
}

.translation-message {
  text-decoration: underline;
  cursor: pointer;
  color: #2e6da4;
}

.translation-info {
  margin-top: 7px;
  display: inline-block;
  position: relative;
}

.translation-message-status {
  line-height: 34px;
}

.translation-message-status-div {
  height: 34px;
}

.translation-input {
  max-width: 100%;
}

.action-container {
  width: 100px;
}

.locale-message-header {
  min-width: 100px;
}

.tag-choice {
  margin-left: 10px;
}

.tags-label {
  margin-left: 0.5em;
  margin-right: 0.5em;
}

.domains-label {
  margin-left: 0.5em;
  margin-right: 0.5em;
}

.empty-message {
  color: darkorange;
}